import { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Paper,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton,
} from '@mui/material';
import { ArrowLeft, BanknoteIcon as Bank } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

const banks = [
  { id: 1, name: 'Bank of America' },
  { id: 2, name: 'Chase' },
  { id: 3, name: 'Wells Fargo' },
  { id: 4, name: 'Citibank' },
  // Add more banks as needed
];

export default function LinkBankPage() {
  const navigate = useNavigate();
  const [selectedBank, setSelectedBank] = useState(null);

  const handleBankSelect = (bank) => {
    setSelectedBank(bank);
    // Here you would typically initiate the Plaid Link process
    console.log(`Selected bank: ${bank.name}`);
    // For demonstration purposes, we'll just navigate back to the payments page
    // In a real application, you'd integrate with Plaid here
    setTimeout(() => {
      navigate('/payments');
    }, 1000);
  };

  return (
    <Box
      sx={{
        minHeight: '100vh',
        backgroundColor: '#1A1B1E',
        py: 3,
      }}
    >
      <Container maxWidth="md">
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 6 }}>
          <IconButton 
            onClick={() => navigate('/dashboard/manage-cards')}
            sx={{ 
              color: 'white',
              mr: 2,
              '&:hover': { backgroundColor: 'rgba(255, 255, 255, 0.1)' }
            }}
          >
            <ArrowLeft />
          </IconButton>

          <Typography
            variant="h4"
            sx={{
              color: '#2DD4BF',
              fontWeight: 600,
              letterSpacing: '-0.02em',
            }}
          >
            Link a Bank Account
          </Typography>
        </Box>

        <Paper
          sx={{
            backgroundColor: 'rgba(26, 27, 30, 0.8)',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            borderRadius: 2,
            p: 4,
          }}
        >
          <Typography variant="h6" sx={{ color: 'white', mb: 2 }}>
            Select your bank
          </Typography>

          <List>
            {banks.map((bank) => (
              <ListItem
                key={bank.id}
                button
                onClick={() => handleBankSelect(bank)}
                sx={{
                  '&:hover': {
                    backgroundColor: 'rgba(45, 212, 191, 0.1)',
                  },
                }}
              >
                <ListItemIcon>
                  <Bank color="#2DD4BF" />
                </ListItemIcon>

                <ListItemText 
                  primary={bank.name}
                  sx={{ color: 'white' }}
                />
              </ListItem>
            ))}
          </List>
        </Paper>
      </Container>
    </Box>
  );
}

